.task {
    position: relative;
    width: 350px;
    padding: 0.4em 2em 0.4em 0.4em;
    margin: 6px auto;
    text-align: center;
    border-radius: 0.2em;
    background-color: #ddd;
}

.task__title {
    font-size: 24px;
}

.task__message {
    font-size: 14px;
    text-align: left;
}

.task__actions {
    position: absolute;
    right: 4px;
    top: 50%;
    display: flex;
    align-items: center;
    transform: translateY(-50%);
}



.task-btn {
    color: gray;
    font-size: 20px;
    margin-left: 2px;
    padding: .2em;
    background-color: #eee;
    border-radius: 50%;
    cursor: pointer;
    aspect-ratio: 1;
    transition: background-color 200ms, color 200ms;
}

.task-delete-btn:hover,
.task-check-btn:hover {
    background-color: #999;
}

.task-delete-btn:hover {
    color: #aa0000;
}

.task-check-btn:hover {
    color: #00aa00;
}